@import url('http://fonts.cdnfonts.com/css/voice-in-my-head');

.landing {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    /* background-image: url('../images/183.jpg'); */
    background-size: cover;
    background-repeat: no-repeat;


}

.header {
    position: absolute;
    top: 1rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
}

.header-wrap {
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60vw;
}

.header-left {
    display: flex;
    align-items: center;
    gap: 2rem;
}

.socials {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 10rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    z-index: 2;
}

.social-color-icon {
    font-size: 30px;
    /* animation: colorIconAnimation 0.2s infinite; */
}

.social-color-icon:hover {
    animation: colorIconAnimation 0.2s infinite;
}


@keyframes colorIconAnimation {
    0% {
        text-shadow: 5px 4px red, -5px -6px blue;
    }

    25% {
        text-shadow: 5px -6px red, -5px 4px blue;

    }

    50% {
        text-shadow: 5px -4px red, -8px 4px blue;

    }

    75% {
        text-shadow: -8px -4px red, -5px -4px blue;

    }

    100% {
        text-shadow: 5px 0 red, 5px -4px blue;

    }

}

.scroll-down {
    position: absolute;
    bottom: 3rem;
    left: 50%;
    transform: translateX(-50%);
}

.scroll-down-wheel {
    height: 42px;
    width: 24px;
    border-radius: 10px;
    border: 2px solid black;
}

.scroll-down-wheel-inner {
    position: absolute;
    left: 10px;
    margin-top: 5px;
    width: 4px;
    height: 4px;
    background: black;
    border-radius: 50%;
    animation: moving-wheel 0.5s linear infinite;
}


@keyframes moving-wheel {
    0% {
        top: 1px;
    }

    25% {
        top: 2px;
    }

    50% {
        top: 3px;
    }

    75% {
        top: 2px;
    }

    100% {
        top: 1px;
    }

}

.scroll-down-arrows {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 45px;

}

.scroll-down-arrow {
    width: 15px;
    height: 15px;
    border-right: 2px solid black;
    border-bottom: 2px solid black;
    transform: rotate(45deg);
    animation: scrollAnimation 1s infinite;

}

.scroll-down-arrow:nth-child(1) {
    animation-delay: 0.1s;
}

.scroll-down-arrow:nth-child(2) {
    animation-delay: 0.2s;
}

.scroll-down-arrow:nth-child(3) {
    animation-delay: 0.3s;
}

@keyframes scrollAnimation {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }

}

.imoji {
    width: 20px;
}

.intro-text {
    position: absolute;
    top: 520%;
    left: -22%;
    width: 100%;
    height: 60%;
    z-index: 2;

}

h4 {
    font-size: 28px;
    font-family: 'Voice In My Head', sans-serif;
                                                
}

h1 {
    font-size: 30px;
    width: 93%;
    padding-top: 1rem;
    font-family: 'Voice In My Head', sans-serif;
                                                
}